.projects {
    min-height: 100vh;
    background-color: black;
    display: flex;
    justify-content: center;
}


.container {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 100px 0px;

}


.line {
    height: 1.5px;
    width: 140px;
    background-color: #ffffff;
    margin-top: 25px;
}


.projectsWrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    margin-top: 26px;
}

